import React, { useEffect } from "react";
import "./Products.css";
import red from "../../images/lockred.png";
import pink from "../../images/lockpink.png";
import purple from "../../images/lockpurple.png";
import green from "../../images/lockgreen.png";
import black from "../../images/cyclelock_new.png";
import scrollreveal from "scrollreveal";

function Products() {
  useEffect(() => {
    const sr = scrollreveal({
      distance: "60px",
      duration: 2500,
      delay: 400,
    });

    sr.reveal(`.products__card`, {
      delay: 500,
      origin: "bottom",
    });
    sr.reveal(`.products__img`, { delay: 700, interval: 200, origin: "top" });
  });

  return (
    <div>
      <section className="products section" id="products">
        <h2 className="section__title section__title-gradient products__line">
          Choose <br /> Your Style
        </h2>

        <div className="products__container container grid">
          <div className="products__card">
            <img src={black} alt="" className="products__img" />
            <div className="products__content">
              <h3 className="products__title">Black</h3>
              <span className="products__price">₹800</span>

              <button className="button button--flex products__button">
                <i className="ri-shopping-bag-line button__icon">
                  <svg
                    width="20"
                    height="20"
                    viewBox="0 0 20 20"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlnsXlink="http://www.w3.org/1999/xlink"
                  >
                    <rect width="20" height="20" fill="url(#pattern0)" />
                    <defs>
                      <pattern
                        id="pattern0"
                        patternContentUnits="objectBoundingBox"
                        width="1"
                        height="1"
                      >
                        <use
                          xlinkHref="#image0_4_4"
                          transform="scale(0.00195312)"
                        />
                      </pattern>
                      <image
                        id="image0_4_4"
                        width="512"
                        height="512"
                        xlinkHref="data:image/png;base64,"
                      />
                    </defs>
                  </svg>
                </i>
              </button>
            </div>
          </div>
          <div className="products__card">
            <div className="products__content">
              <img src={red} alt="" className="products__img" />

              <h3 className="products__title">Red</h3>
              <span className="products__price">₹800</span>

              <button className="button button--flex products__button">
                <i className="ri-shopping-bag-line button__icon">
                  <svg
                    width="20"
                    height="20"
                    viewBox="0 0 20 20"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlnsXlink="http://www.w3.org/1999/xlink"
                  >
                    <rect width="20" height="20" fill="url(#pattern0)" />
                    <defs>
                      <pattern
                        id="pattern0"
                        patternContentUnits="objectBoundingBox"
                        width="1"
                        height="1"
                      >
                        <use
                          xlinkHref="#image0_4_4"
                          transform="scale(0.00195312)"
                        />
                      </pattern>
                      <image
                        id="image0_4_4"
                        width="512"
                        height="512"
                        xlinkHref="data:image/png;base64,"
                      />
                    </defs>
                  </svg>
                </i>
              </button>
            </div>
          </div>
          <div className="products__card">
            <div className="products__content">
              <img src={green} alt="" className="products__img" />

              <h3 className="products__title">Green</h3>
              <span className="products__price">₹800</span>

              <button className="button button--flex products__button">
                <i className="ri-shopping-bag-line button__icon">
                  <svg
                    width="20"
                    height="20"
                    viewBox="0 0 20 20"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlnsXlink="http://www.w3.org/1999/xlink"
                  >
                    <rect width="20" height="20" fill="url(#pattern0)" />
                    <defs>
                      <pattern
                        id="pattern0"
                        patternContentUnits="objectBoundingBox"
                        width="1"
                        height="1"
                      >
                        <use
                          xlinkHref="#image0_4_4"
                          transform="scale(0.00195312)"
                        />
                      </pattern>
                      <image
                        id="image0_4_4"
                        width="512"
                        height="512"
                        xlinkHref="data:image/png;base64,"
                      />
                    </defs>
                  </svg>
                </i>
              </button>
            </div>
          </div>
          <div className="products__card">
            <div className="products__content">
              <img src={purple} alt="" className="products__img" />

              <h3 className="products__title">Purple</h3>
              <span className="products__price">₹800</span>

              <button className="button button--flex products__button">
                <i className="ri-shopping-bag-line button__icon">
                  <svg
                    width="20"
                    height="20"
                    viewBox="0 0 20 20"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlnsXlink="http://www.w3.org/1999/xlink"
                  >
                    <rect width="20" height="20" fill="url(#pattern0)" />
                    <defs>
                      <pattern
                        id="pattern0"
                        patternContentUnits="objectBoundingBox"
                        width="1"
                        height="1"
                      >
                        <use
                          xlinkHref="#image0_4_4"
                          transform="scale(0.00195312)"
                        />
                      </pattern>
                      <image
                        id="image0_4_4"
                        width="512"
                        height="512"
                        xlinkHref="data:image/png;base64,"
                      />
                    </defs>
                  </svg>
                </i>
              </button>
            </div>
          </div>

          <div className="products__card">
            <div className="products__content">
              <img src={pink} alt="" className="products__img" />

              <h3 className="products__title">Pink</h3>
              <span className="products__price">₹800</span>

              <button className="button button--flex products__button">
                <i className="ri-shopping-bag-line button__icon">
                  <svg
                    width="20"
                    height="20"
                    viewBox="0 0 20 20"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlnsXlink="http://www.w3.org/1999/xlink"
                  >
                    <rect width="20" height="20" fill="url(#pattern0)" />
                    <defs>
                      <pattern
                        id="pattern0"
                        patternContentUnits="objectBoundingBox"
                        width="1"
                        height="1"
                      >
                        <use
                          xlinkHref="#image0_4_4"
                          transform="scale(0.00195312)"
                        />
                      </pattern>
                      <image
                        id="image0_4_4"
                        width="512"
                        height="512"
                        xlinkHref="data:image/png;base64,"
                      />
                    </defs>
                  </svg>
                </i>
              </button>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Products;
